body {
  font-family: "Roboto Slab", serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-color: #f6ae2d;
}

#quote-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 2%;
  padding: 2em;
  max-width: 30em;
  min-height: 25em;
  background-color: #a3c3f7;
}

#div1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 20em;
}

#text {
  text-align: center;
  color: #091540;
  font-size: 2em;
}

#author {
  text-align: right;
  color: #3a3042;
  font-size: 1.5em;
}

#div2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

button {
  width: 10em;
  height: 3em;
  background-color: #ff784f;
  color: white;
  border: none;
  font-size: 1em;
  font-family: "Roboto Slab", serif;
  border: 2px solid #ff784f;
}

button:hover {
  background-color: white;
  color: #ff784f;
}

button:focus {
  outline: none;
  box-shadow: -3px 5px #1052bc;
}

button:active {
  background-color: #ffd1c2;
  color: #ff784f;
}

@media (width < 30em) {
  #quote-box {
    width: 80vw;
    padding: 1em;
  }
  #text {
    font-size: 1.5em;
  }
  #author {
    font-size: 1em;
  }
  button {
    width: 8em;
    height: 3em;
    font-size: 0.8em;
  }
}
